.list-group {
  padding: 0;
  list-style: none;
  width: 100%;
}

.list-group-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgb(17, 107, 17);
  padding: 10px;
}

.btn {
  background-color: white;
  cursor: pointer;
  color: red;
  border: 1px solid red;
}